body {
  background: #f9f3d8;
  height: 100%;
  margin: 0;
}

/*  To Keep Footer on the bottom. */
.body-container-flex {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
p {
  line-height: 2rem;
}
.articleContainerMobile {
  width: 60%;
}
@media (max-width: 450px) {
  .articleContainerMobile {
    width: 95%;
  }
  .articleImageContainer {
    margin-bottom: 5px;
  }
}
