.footer {
  background: rgb(34, 34, 34);
  background: radial-gradient(
    circle,
    rgba(34, 34, 34, 1) 56%,
    rgba(0, 0, 0, 1) 100%
  );
  color: #fafafa;
  padding-top: 3rem;
  width: 100%;
}
.mobileFooter {
  width: 30%;
}
@media (max-width: 800px) {
  .mobileFooter {
    width: 50%;
  }
}
@media (max-width: 450px) {
  .mobileFooter {
    width: 100%;
  }
}
