.imageArticle {
  float: right;
  margin: 0px 10px 10px 10px;
}

.affiliateButton {
  display: block;
  background-color: #85bb65;
  width: 250px;
  color: white;
  text-align: center;
  padding: 5px;
  border-radius: 10px;
  margin: 0 auto 0 auto;
}
.affiliateButton:hover {
  transform: scale(1.02);
  cursor: pointer;
  color: white;
}
@media (max-width: 450px) {
  .imageArticle {
    float: none;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
}
